import { api_stub_get, api_stub_post } from "./_stub";
import { URL } from "../utils/_consts";

export async function sales_list(depot, start_date, end_date) {
    start_date = new Date(start_date)
    start_date = `${start_date.getFullYear()}-${start_date.getMonth() + 1}-${start_date.getDate()}`
    end_date = new Date(end_date)
    end_date = `${end_date.getFullYear()}-${end_date.getMonth() + 1}-${end_date.getDate()}`

    return api_stub_get(`/sales/?depot=${depot}&start_date=${start_date}&end_date=${end_date}`, depot = true)
}

export async function sales_create(start_date, end_date, product, lpo_no, customer, truck, depot, entry_no, order_no, date, vol_obs, vol_20, selling_price, paid) {

    start_date = new Date(start_date)
    start_date = `${start_date.getFullYear()}-${start_date.getMonth() + 1}-${start_date.getDate()}`
    end_date = new Date(end_date)
    end_date = `${end_date.getFullYear()}-${end_date.getMonth() + 1}-${end_date.getDate()}`
    const body = {
        product: product,
        lpo_no: lpo_no,
        customer: customer,
        truck: truck,
        depot: depot,
        entry_no: entry_no,
        order_no: order_no,
        date: date,
        vol_obs: vol_obs,
        vol_20: vol_20,
        selling_price: selling_price,
        is_paid: paid
    }

    return api_stub_post(`/sales/?start_date=${start_date}&end_date=${end_date}`, body)
}

export async function upload_excel(file, depot_id) {

    try {
        let formData = new FormData()
        formData.append("file", file)
        let rData = await fetch(`${URL}/sales/${depot_id}/upload/`, {
            method: 'POST',
            headers: {},
            body: formData
        });
        let data = await rData.json();
        rData = null;
        return data
    }
    catch (error) {
        console.log(error)
        throw error;

    }
}