import { URL } from "../utils/_consts";

export async function get_token(username, password) {

    try {
        let raw_data = await fetch(`${URL}/accounts/get-token/`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                username: username,
                password: password,
            })

        });
        let data = await raw_data.json();
        raw_data = null;
        return data
    }
    catch (error) {
        console.log(error)
        throw error;

    }
}
