import { URL } from "../utils/_consts";
import { store } from "../redux/store"
import { UPDATE_CURRENCY_TYPE_ACTION } from "../redux/actions/actions";

export const api_stub_get = async (extension, depot = false) => {
    var token = store.getState().auth_reducer.token
    try {
        let raw_data = await fetch(`${URL}${extension}`, {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                "Authorization": `Token ${token.token}`
            },

        });
        let data = await raw_data.json();
        if (token.is_staff === false && depot === false) {
            data = data.filter(obj => {
                return obj.name === token.depot
            })
        }
        raw_data = null;

        return data
    }
    catch (error) {
        console.log(error)
        throw error;

    }
}

export const api_stub_post = async (extension, api_body) => {
    var token = store.getState().auth_reducer.token
    try {
        let raw_data = await fetch(`${URL}${extension}`, {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                "Authorization": `Token ${token.token}`
            },
            body: JSON.stringify(api_body)
        });
        let data = await raw_data.json();
        raw_data = null;
        return data
    }
    catch (error) {
        console.log(error)
        throw error;

    }
}