import { api_stub_get } from "./_stub";


export async function product_series(start_date, end_date) {
    if (typeof (start_date) !== "object" && typeof (end_date) !== "object") {
        start_date = new Date(start_date)
        end_date = new Date(end_date)
    }

    start_date = `${start_date.getFullYear()}-${start_date.getMonth() + 1}-${start_date.getDate()}`

    end_date = `${end_date.getFullYear()}-${end_date.getMonth() + 1}-${end_date.getDate()}`
    return api_stub_get(`/product/series/?start_date=${start_date}&end_date=${end_date}`)
}

export async function product_month(year) {
    return api_stub_get(`/product/month/?year=${year}`)
}

export async function product_top_customer(year, month) {
    return api_stub_get(`/product/customer/top/?year=${year}&month=${month}`)
}

export async function product_list() {
    return api_stub_get("/product/", true)
}