// AUTHENTICATION
export const GET_TOKEN_ACTION = (username, password) => async dispatch => {
    await get_token(username, password).then(data => {
        if (data.token !== undefined) {
            dispatch({ type: GET_TOKEN, payload: data })
        }
        else {
            try {
                let errors = data.non_field_errors
                dispatch({ type: GET_TOKEN_ERROR, payload: { "": errors } })
            } catch (e) {
                dispatch({ type: GET_TOKEN_ERROR, payload: data })
            }
        }
    })
        .catch(error => {
            console.log(error);
        })
}

export const GET_TOKEN_ERROR_REMOVE_ACTION = () => dispatch => {
    dispatch({ type: GET_TOKEN_ERROR_REMOVE, payload: null })
}

export const USER_LOGGED_OUT_ACTION = () => dispatch => {
    dispatch({ type: USER_LOGGED_OUT, payload: null })
}

