export const TRUCK_CREATE_ACTION = (customer, plate_no, first_name, last_name) => async dispatch => {
    await truck_create(customer, plate_no, first_name, last_name).then(data => {
        if (customer === null) {
            dispatch({ type: GET_TRUCK_LIST, payload: data })
        } else {
            dispatch({ type: GET_CUSTOMER_LIST, payload: data })
        }
    })
        .catch(error => {
            console.log(error);
        })
}

