export const UPLOAD_EXCEL_ACTION = (file, depot_id) => async dispatch => {

    await upload_excel(file, depot_id).then(data => {
        if (data.status === "success") {
            dispatch({ type: UPLOAD_EXCEL, payload: data })
        } else {
            dispatch({ type: UPLOAD_EXCEL_ERROR, payload: data })
        }
    })
        .catch(error => {
            console.log(error);
        })
}
