import { api_stub_get, api_stub_post } from "./_stub";

export async function top_customer(year, month) {

    return api_stub_get(`/customer/top/month/1/?year=${year}&month=${month}`)
}

export async function customer_list() {

    return api_stub_get("/customer/", true)
}

export async function customer_detail(customer_id) {
    return api_stub_get(`/customer/${customer_id}/`)
}

export async function customer_create(code, name) {
    const body = {
        code: code,
        name: name,
    }
    return api_stub_post(`/customer/`, body)
}

export async function truck_create(customer, plate_no, first_name, last_name) {
    const body = {
        customer: customer,
        plate_no: plate_no,
        first_name: first_name,
        last_name: last_name,
    }
    return api_stub_post(`/customer/trucks/`, body)
}

export async function truck_list() {
    return api_stub_get(`/customer/trucks/`)
}
export async function add_customer(depot, customer) {
    const body = {
        depot: depot,
        customer: customer,
    }
    return api_stub_post(`/customer/add/`, body)
}