import { api_stub_get } from "./_stub";

export async function depot_list() {
    return api_stub_get("/depot/")
}

export async function depot_month(year) {
    return api_stub_get(`/depot/month/?year=${year}`)
}
export async function depot_product_month(year) {

    return api_stub_get(`/product/depot/month/?year=${year}`)
}

export async function depot_series(id, start_date, end_date) {
    if (typeof (start_date) !== "object" && typeof (end_date) !== "object") {
        start_date = new Date(start_date)
        end_date = new Date(end_date)
    }


    start_date = `${start_date.getFullYear()}-${start_date.getMonth() + 1}-${start_date.getDate()}`

    end_date = `${end_date.getFullYear()}-${end_date.getMonth() + 1}-${end_date.getDate()}`
    const d = api_stub_get(`/depot/series/${id}/?start_date=${start_date}&end_date=${end_date}`, true)
    return d
}

export async function depot_product_series(id, start_date, end_date) {

    if (typeof (start_date) !== "object" && typeof (end_date) !== "object") {
        start_date = new Date(start_date)
        end_date = new Date(end_date)
    }

    start_date = `${start_date.getFullYear()}-${start_date.getMonth() + 1}-${start_date.getDate()}`

    end_date = `${end_date.getFullYear()}-${end_date.getMonth() + 1}-${end_date.getDate()}`
    return api_stub_get(`/depot/product/series/${id}/?start_date=${start_date}&end_date=${end_date}`, true)
}

export async function depot_customer(id, year, month) {
    return api_stub_get(`/depot/customer/${id}/?year=${year}&month=${month}`, true)
}